class Row extends React.Component{
    /*constructor(props){
        super(props)
    }*/
    render(){
        let courses = [
            {
                sku: '1.1.4',
                name: 'Corso 1',
                state: true,
                price: 100
            },
            {
                sku: '1.1.8',
                name: 'Corso 2',
                state: false,
                price: 100
            },
            {
                sku: '1.2.6',
                name: 'Corso 3',
                state: false,
                price: 100
            }
        ]
        return(
            <>
                {
                    courses.map((course,i)=>{
                        return <div className="row" key={"course-"+i}>
                            <div className="col-1">
                                {course.sku}
                            </div>
                            <div className="col">
                                {course.name}
                            </div>
                            <div className="col-2 pl-4">
                                {course.price}
                            </div>
                            <div className="col text-center text-white pl-1">
                                {course.state?
                                    <div className="bg-success rounded wFit px-2">
                                        Si
                                    </div>
                                    :
                                    <div className="bg-danger rounded wFit px-2">
                                        No
                                    </div>
                                }
                            </div>
                            <div className="col row">
                                <Link to="/courses/update" className="col bg-warning textWhite h30 rounded-left borderMedium shadowInsetBottom pointer text-center">
                                    Modifica
                                </Link>
                                <Link to="/courses/modules" className="col bg-white h30 borderMedium shadowInsetBottom pointer text-center">
                                    Moduli
                                </Link>
                                <div className="col bg-danger textWhite h30 rounded-right borderMedium shadowInsetBottom pointer text-center">
                                    Elimina
                                </div>
                            </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </>
        )
    }
}
