class Row extends React.Component{
    /*constructor(props){
        super(props)
    }*/
    render(){
        let categories = [
            {
                id: 1,
                name: 'Corsi Patentini Attrezzature',
                state: true
            },
            {
                id: 2,
                name: 'Corsi Sicurezza Lavoro',
                state: false
            },
            {
                id: 3,
                name: 'Corsi Aggiuntivi',
                state: true
            },
        ]
        return(
            <>
                {
                    categories.map((category,i)=>{
                        return <div className="row" key={"category-"+i}>
                            <div className="col-1">
                                {category.id}
                            </div>
                            <div className="col">
                                {category.name}
                            </div>
                            <div className="col-2 text-center text-white pl-1">
                                {category.state?
                                    <div className="bg-success rounded wFit px-2">
                                        On-line
                                    </div>
                                    :
                                    <div className="bg-danger rounded wFit px-2">
                                        Off-line
                                    </div>
                                }
                            </div>
                            <div className="col row">
                                <Link to="/" className="col bg-warning textWhite h30 rounded-left borderMedium shadowInsetBottom pointer text-center">
                                    Modifica
                                </Link>
                                <Link to="/" className="col bg-white h30 borderMedium shadowInsetBottom pointer text-center">
                                    Organizza
                                </Link>
                                <div className="col bg-danger textWhite h30 rounded-right borderMedium shadowInsetBottom pointer text-center">
                                    Elimina
                                </div>
                            </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </>
        )
    }
}
